.Contenedor-Card {
  border: 1px solid;
  width: 450px;
  margin: 1rem;
  border-radius: 10px;
  padding: 2rem;
  background: white;
}
.Contenedor-Card .button {
  text-align: center;
}
.Contenedor-Card button {
  width: 90%;
  padding: 6px;
  background: rgb(139 139 236);
  border-radius: 5px;
  color: white;
}
@media (max-width: 320px) {
  .Contenedor-Card {
    width: auto;
    margin: 0rem;
  }
}
