.Form {
  display: flex;
  flex-direction: column;
  width: 500px;
}
.Form span {
  text-align: left;
  margin-bottom: 10px;
}
.Form input {
  margin-bottom: 1rem;
  height: 30px;
  border-radius: 5px;
}
.Form textarea {
  margin-bottom: 1rem;
  height: 80px;
  border-radius: 5px;
}
.Form button {
  padding: 10px;
  color: white;
  background-color: rgba(21, 164, 230, 0.652);
}
@media (max-width: 320px) {
  .Form {
    width: auto;
  }
}
